.gql-success-rate-dialog {
  height: calc(100% + 160px);
  margin: 0 -40px;
  background-color: #F0F3F7;
}
.gql-success-rate-dialog__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  padding: 0 40px 0 32px;
  height: 55px;
  background-color: #FFF;
  box-shadow: 0px 1px 6px 0px rgba(49, 53, 59, 0.12);
}
.gql-success-rate-dialog__content {
  padding: 0 32px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.gql-success-rate-dialog__content-row {
  border-radius: 8px;
  background-color: #FFF;
  box-shadow: 0px 1px 6px 0px rgba(49, 53, 59, 0.12);
  padding: 16px;
}
.gql-success-rate-dialog__content-row section {
  margin-bottom: 16px;
}
.gql-success-rate-dialog__content-row section > h4 {
  margin-bottom: 4px;
}
.gql-success-rate-dialog__content-row section pre {
  display: inline;
}
.gql-success-rate-dialog__content-row > div {
  width: 100%;
}

.gql-success-rate {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  background-color: #F0F3F7;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: all cubic-bezier(0.63, 0.01, 0.29, 1) 300ms;
}
.gql-success-rate--loading {
  background-color: #F0F3F7;
}
.gql-success-rate--error, .gql-success-rate--layout-danger {
  background-color: #F94D63;
}
.gql-success-rate--layout-success {
  background-color: #20CE7D;
}
.gql-success-rate--layout-warning {
  background-warning: #FF7F17;
}
.gql-success-rate img {
  position: absolute;
  top: 25px;
  left: 25px;
  width: 60px;
  filter: grayscale(1);
  padding: 10px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.25);
  transform: translate(-50%, -50%);
}
.gql-success-rate__text {
  text-align: right;
  position: absolute;
  right: 16px;
  bottom: 16px;
}
.gql-success-rate__spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}